import React, {Component} from 'react';
import Card from './Card';
import State from './daily/State'
import '../../src/components/Style.css'
import CountUp from 'react-countup';


class Api extends Component {

    state = {
        error: '',
        products: [],
        opt: '',
        stte: []
    }

    change = (e) => {
        this.setState({opt: e.target.value})
        console.log(e.target.value);
        const apiurl = 'https://api.covid19india.org/states_daily.json';

        fetch(apiurl)
            .then(response => response.json())
            .then((result) => {
                this.setState({stte: result.states_daily})

            })
    }

    componentDidMount()
    {
        const apiurl = 'https://api.covid19india.org/data.json';

        fetch(apiurl)
            .then(response => response.json())
            .then((result) => {
                this.setState({products: result.statewise})

            }, (error) => {
                this.setState({error})
            })
    }

    render() {
        var slt,
            act,
            rcd;

        const {error, products, opt, stte} = this.state;
        console.log(error);

        var prnt = products.map((s, i) => {
            return (s.state)
        })
        if (stte) {
            var lnt = stte.length

            // console.log(stte); console.log(lnt); console.log(stte[lnt - 1]);
            // console.log(stte[lnt - 2]); console.log(stte[lnt - 3]);

        }
        if (stte[lnt]) {
            // console.log(stte[lnt - 1].br);

        }

        const statewise = products[opt]

        if (statewise) {
            // console.log("active : " + statewise.active+"\n" + " recovered : " +
            // statewise.recovered+"\n" + " confirmed : " + statewise.confirmed +"\n"+ "
            // death : " + statewise.deaths) console.log(statewise.state);

        }
        if (statewise && stte && lnt) {
            // var lnt = stte.length
            switch (statewise.state) {
                case 'Bihar':
                    slt = stte[lnt - 1].br
                    rcd = stte[lnt - 2].br
                    act = stte[lnt - 3].br
                    break;
                case 'Andaman and Nicobar Islands':
                    slt = stte[lnt - 1].an
                    rcd = stte[lnt - 2].an
                    act = stte[lnt - 3].an
                    break;
                case 'Andhra Pradesh':
                    slt = stte[lnt - 1].ap
                    rcd = stte[lnt - 2].ap
                    act = stte[lnt - 3].ap
                    break;
                case 'Arunachal Pradesh':
                    slt = stte[lnt - 1].ar
                    rcd = stte[lnt - 2].ar
                    act = stte[lnt - 3].ar
                    break;
                case 'Assam':
                    slt = stte[lnt - 1].as
                    rcd = stte[lnt - 2].as
                    act = stte[lnt - 3].as
                    break;
                case 'Chandigarh':
                    slt = stte[lnt - 1].ch
                    rcd = stte[lnt - 2].ch
                    act = stte[lnt - 3].ch
                    break;
                case 'Chhattisgarh':
                    slt = stte[lnt - 1].ct
                    rcd = stte[lnt - 2].ct
                    act = stte[lnt - 3].ct
                    break;
                case 'Delhi':
                    slt = stte[lnt - 1].dl
                    rcd = stte[lnt - 2].dl
                    act = stte[lnt - 3].dl
                    break;
                case 'Dadra and Nagar Haveli and Daman and Diu':
                    slt = stte[lnt - 1].dn
                    rcd = stte[lnt - 2].dn
                    act = stte[lnt - 3].dn
                    break;
                case 'Goa':
                    slt = stte[lnt - 1].ga
                    rcd = stte[lnt - 2].ga
                    act = stte[lnt - 3].ga
                    break;
                case 'Gujarat':
                    slt = stte[lnt - 1].gj
                    rcd = stte[lnt - 2].gj
                    act = stte[lnt - 3].gj
                    break;
                case 'Himachal Pradesh':
                    slt = stte[lnt - 1].hp
                    rcd = stte[lnt - 2].hp
                    act = stte[lnt - 3].hp
                    break;
                case 'Haryana':
                    slt = stte[lnt - 1].hr
                    rcd = stte[lnt - 2].hr
                    act = stte[lnt - 3].hr
                    break;
                case 'Jharkhand':
                    slt = stte[lnt - 1].jh
                    rcd = stte[lnt - 2].jh
                    act = stte[lnt - 3].jh
                    break;
                case 'Jammu and Kashmir':
                    slt = stte[lnt - 1].jk
                    rcd = stte[lnt - 2].jk
                    act = stte[lnt - 3].jk
                    break;
                case 'Karnataka':
                    slt = stte[lnt - 1].ka
                    rcd = stte[lnt - 2].ka
                    act = stte[lnt - 3].ka
                    break;
                case 'Kerala':
                    slt = stte[lnt - 1].kl
                    rcd = stte[lnt - 2].kl
                    act = stte[lnt - 3].kl
                    break;
                case 'Ladakh':
                    slt = stte[lnt - 1].la
                    rcd = stte[lnt - 2].la
                    act = stte[lnt - 3].la
                    break;
                case 'Lakshadweep':
                    slt = stte[lnt - 1].ld
                    rcd = stte[lnt - 2].ld
                    act = stte[lnt - 3].ld
                    break;
                case 'Maharashtra':
                    slt = stte[lnt - 1].mh
                    rcd = stte[lnt - 2].mh
                    act = stte[lnt - 3].mh
                    break;
                case 'Meghalaya':
                    slt = stte[lnt - 1].ml
                    rcd = stte[lnt - 2].ml
                    act = stte[lnt - 3].ml
                    break;
                case 'Manipur':
                    slt = stte[lnt - 1].mn
                    rcd = stte[lnt - 2].mn
                    act = stte[lnt - 3].mn
                    break;
                case 'Madhya Pradesh':
                    slt = stte[lnt - 1].mp
                    rcd = stte[lnt - 2].mp
                    act = stte[lnt - 3].m3
                    break;
                case 'Mizoram':
                    slt = stte[lnt - 1].mz
                    rcd = stte[lnt - 2].mz
                    act = stte[lnt - 3].mz
                    break;
                case 'Nagaland':
                    slt = stte[lnt - 1].nl
                    rcd = stte[lnt - 2].nl
                    act = stte[lnt - 3].nl
                    break;
                case 'Odisha':
                    slt = stte[lnt - 1].or
                    rcd = stte[lnt - 2].or
                    act = stte[lnt - 3].or
                    break;
                case 'Punjab':
                    slt = stte[lnt - 1].pb
                    rcd = stte[lnt - 2].pb
                    act = stte[lnt - 3].pb
                    break;
                case 'Puducherry':
                    slt = stte[lnt - 1].py
                    rcd = stte[lnt - 2].py
                    act = stte[lnt - 3].py
                    break;
                case 'Rajasthan':
                    slt = stte[lnt - 1].rj
                    rcd = stte[lnt - 2].rj
                    act = stte[lnt - 3].rj
                    break;
                case 'Sikkim':
                    slt = stte[lnt - 1].sk
                    rcd = stte[lnt - 2].sk
                    act = stte[lnt - 3].sk
                    break;
                case 'Telangana':
                    slt = stte[lnt - 1].tg
                    rcd = stte[lnt - 2].tg
                    act = stte[lnt - 3].tg
                    break;
                case 'Tamil Nadu':
                    slt = stte[lnt - 1].tn
                    rcd = stte[lnt - 2].tn
                    act = stte[lnt - 3].tn
                    break;
                case 'Tripura':
                    slt = stte[lnt - 1].tr
                    rcd = stte[lnt - 2].tr
                    act = stte[lnt - 3].tr
                    break;
                case 'Total':
                    slt = stte[lnt - 1].tt
                    rcd = stte[lnt - 2].tt
                    act = stte[lnt - 3].tt
                    break;
                case 'State Unassigned':
                    slt = stte[lnt - 1].un
                    rcd = stte[lnt - 2].un
                    act = stte[lnt - 3].un
                    break;
                case 'Uttar Pradesh':
                    slt = stte[lnt - 1].up
                    rcd = stte[lnt - 2].up
                    act = stte[lnt - 3].up
                    break;
                case 'Uttarakhand':
                    slt = stte[lnt - 1].ut
                    rcd = stte[lnt - 2].ut
                    act = stte[lnt - 3].ut
                    break;
                case 'West Bengal':
                    slt = stte[lnt - 1].wb
                    rcd = stte[lnt - 2].wb
                    act = stte[lnt - 3].wb
                    break;
                default:
                    slt = "UNAVAILABLE"
                    rcd = "UNAVAILABLE"
                    act = "UNAVAILABLE"
            }
            if (slt) {
                console.log(slt); console.log(rcd); console.log(act);
            }
            // console.log(stte[lnt - 1].br);
        }
        if (slt) {
            // console.log(slt);
        }

        return (
            <div className="">
                <div className="">
                    <div className="text-center">
                        <select
                            name=""
                            defaultValue={0}
                            id=""
                            onChange={this.change}
                            className="col"
                            style={{
                            border: "none",
                            borderBottom: "3px solid black",
                            width: "260px"
                        }}>
                            <option disabled selected className='text-caitalize'>
                                select state
                            </option>
                            {prnt.map((s, i) => <option key={i} value={i}>
                                {s
}
                            </option>)}
                        </select>
                    </div>
                    <Card info ={products[opt]}/>

{slt ?                    <ul className="text-center dwn rsp d-flex justify-content-center mb-0">         
                    <li className="alert alert-primary text-capitalize ">Today : &nbsp; &nbsp;
                    <hr/>
                        <span className="card-text ">confirmed&nbsp;{< CountUp className="text-dark" start = {
                                0
                            }
                            end = {
                                act
                            }
                            duration = {
                                1.5
                            }
                            separator = ',' />}</span>
                        <hr/>
                        <span className="card-text ">Deceased&nbsp;{< CountUp className="text-dark" start = {
                                0
                            }
                            end = {
                                slt
                            }
                            duration = {
                                1.5
                            }
                            separator = ',' />}</span>
                            <hr/>
                        <span className="card-text ">recovered&nbsp;{< CountUp className="text-dark" start = {
                                0
                            }
                            end = {
                                rcd
                            }
                            duration = {
                                1.5
                            }
                            separator = ',' />}</span>

                    </li>
                    </ul>:null}

                    
                    
                    

                </div>
                {/* {products ? <h1>{products[opt].active}</h1>:<h1>avinash</h1>} */}
                {statewise
                    ? <State info ={statewise.state}/>
                    : null}
            </div>

        )

    }
}

export default Api;
